import React, { Component } from "react";
import "./style.css";
import "bootstrap/dist/css/bootstrap.css";

class Codeforces extends Component {
  constructor() {
    super();
    this.state = {
      ratedcontest: [],
      profile: []
    };
  }

  componentDidMount() {
    fetch("https://codeforces.com/api/user.rating?handle=angkon")
      .then(Response => Response.json())
      .then(data => this.setState({ ratedcontest: data.result }));

    fetch(" https://codeforces.com/api/user.info?handles=angkon")
      .then(Response => Response.json())
      .then(data => this.setState({ profile: data.result[0] }));
  }

  render() {
    let i = 0;
    console.log(this.state.profile.lastName);
    return (
      <div className="container-fluid nav-fixed">
        <div className="row">
          <div className="col-xs-12 col-md-3">
            <div className="container-fluid float-center">
              <img class="cfimage" src={this.state.profile.avatar} />
              <h2 className="mb-2">
                {this.state.profile.firstName +
                  " " +
                  this.state.profile.lastName}
              </h2>
              <p className="h6">
                <span class="badge badge-light">Rank :</span>{" "}
                {this.state.profile.rank}{" "}
                <span class="badge badge-light"> Max Rank :</span>{" "}
                {this.state.profile.maxRank}
              </p>
              <p className="h6">
                <span class="badge badge-light">Rating :</span>{" "}
                {this.state.profile.rating}{" "}
                <span class="badge badge-light">Max Rating :</span>{" "}
                {this.state.profile.maxRating}
              </p>
              <p className="h6">
                <span class="badge badge-light">Organization :</span>{" "}
                {this.state.profile.organization}
              </p>
              <p className="h6">
                <span class="badge badge-light">Friend</span> of{" "}
                {this.state.profile.friendOfCount} users
              </p>
            </div>
          </div>
          <div className="col-xs-12 col-md-8">
            {!this.state.ratedcontest.length === 0 ? (
              <h1>No data</h1>
            ) : (
              <h3>
                Contest History of{" "}
                <a target="blank" href="https://codeforces.com/profile/angkon">
                  Jadir Ibna Hasan
                </a>
              </h3>
            )}
            <div className="mt-">
              <table class="table table-bordered">
                <thead class="thead-dark">
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Contest</th>
                    <th scope="col">Rank</th>
                    <th scope="col">Rating Change</th>
                    <th scope="col"> New Rating</th>
                  </tr>
                </thead>
                {this.state.ratedcontest
                  .sort((a, b) =>
                    a.ratingUpdateTimeSeconds < b.ratingUpdateTimeSeconds
                      ? 1
                      : -1
                  )
                  .map(contest => (
                    <tbody>
                      <tr class="table-success">
                        <th scope="row">
                          {this.state.ratedcontest.length - i++}
                        </th>
                        <td>
                          <a>{contest.contestName}</a>
                        </td>
                        <td>
                          <a>{contest.rank}</a>
                        </td>

                        <td>
                          <span>{contest.newRating - contest.oldRating}</span>
                        </td>
                        <td>{contest.newRating}</td>
                      </tr>
                    </tbody>
                  ))}
              </table>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Codeforces;
