import React, { Component } from "react";
import profile from "../staticStore/profileDetails";
import "./style.css";

class Profile extends Component {
  constructor() {
    super();
    this.state = { profile: profile };
  }
  render() {
    return (
      <div id="body" className="container nav-fixed">
        <div className="row">
          <div className="col-xs-12 col-md-4">
            <div className="image-box mb-3">
              <img class="rounded-circle imgw" src={this.state.profile.img} />
            </div>
            <div className="profile-name">
              <h3>
                {this.state.profile.name} {this.state.profile.nick_name}
              </h3>
            </div>
            <div>
              <p class="txtcol">Computer Science and Engineering</p>
              <p class="h5 orange">United International University</p>
            </div>
            <div>
              {this.state.profile.links.map(link => (
                <a class="ml-3" target="blank" href={link.url}>
                  {link.linkname}
                </a>
              ))}
            </div>
          </div>
          <div className="col-xs-12 col-md-8 mtop">
            <div class="mt-5 mb-2">
              <h1>Biography</h1>
            </div>
            <div>
              <p class="fw">{this.state.profile.bio}</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Profile;
