import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Navbar from "./Components/Navbar";
import MainContent from "./Components/MainContent";
import Footer from "./Components/Footer";
import Codeforces from "./Components/Codeforces";
import Github from "./Components/Github";
import Project from "./Components/Projects";
import Education from "./Components/Education";
import Profile from "./Components/Profile";
import Others from "./Components/Others";

import "./App.css";

class App extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    return (
      <Router>
        <div className="App">
          <Navbar />

          <Switch>
            <Route path="/github" render={() => <Github />} />
            <Route path="/codeforces" render={() => <Codeforces />} />
            <Route path="/motivation" render={() => <MainContent />} />
            <Route path="/projects" render={() => <Project />} />
            <Route path="/education" render={() => <Education />} />
            <Route path="/others" render={() => <Others />} />
            <Route path="" render={() => <Profile />} />
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;
