import React, { Component } from "react";
import "./style.css";
import projectList from "../staticStore/projectslist";

class Project extends Component {
  constructor() {
    super();
    this.state = {
      projects: projectList
    };
  }
  render() {
    return (
      <div className="container nav-fixed">
        {this.state.projects.map(project => (
          <div className="row justify-content-md-center mb-5 shadow-lg p-3 mb-5 bg-white rounded">
            <div className="image-box mb-3">
              <img class="ins5 text-center" src={project.img} />
            </div>
            <div class="mt-2">
              <h1>{project.project_name}</h1>
              <div className="txtcol">
                <h4>{project.details}</h4>
              </div>
            </div>
        </div>
          ))}
      </div>
    );
  }
}
export default Project;
