import React, { Component } from "react";
import edulist from "../staticStore/edulist";

class Education extends Component {
  constructor() {
    super();
    this.state = {
      educations: edulist
    };
  }

  render() {
    return (

      <div className="container nav-fixed">
        {this.state.educations.map(edu => (
          <div className="row justify-content-md-center mb-5 shadow-lg p-3 mb-5 bg-white rounded">
          <div className="col-xs-12 col-md-4">
            <div className="image-box mb-3">
              <img class="ins"src={edu.img} />
            </div>
          </div>
          <div className="col-xs-12 col-md-8 mtop">
            <div class="mt-5">
              <h1>{edu.institute_name}</h1>
              <div className="txtcol">
                <h4>{edu.degree}</h4>
                <h4>{edu.year_of_passing}</h4>
              </div>
            </div>
          </div>
        </div>
          ))}
      </div>
      
    );
  }
}

export default Education;
